html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

.landing-page {
  background-image: url("https://images.unsplash.com/photo-1555766720-1e727844cc8f?q=80&w=1965&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
  background-size: cover;
  background-position: center;
  padding: 20px;
  height: 100vh; 
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: left;
}

.text {
  font-size: larger;
  font-style: italic;
}

.card {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  width: 300px; 
}

.navlinks {
  text-decoration: none;
  color: black;
  padding: 10px 20px; 
  margin-right: 20px; 
  border-radius: 5px; 
  background-color: lightgray; 
}

.navlinks:hover {
  background-color: gray; 
  color: white; 
}
